<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item class="activepage"></el-breadcrumb-item>
        <el-breadcrumb-item ></el-breadcrumb-item>
        <el-breadcrumb-item ></el-breadcrumb-item>
        <el-breadcrumb-item ></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
        <el-row :gutter="30">
            <el-col :span="10">
                <!--  -->
                <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
                    <el-button slot="append" icon="iconfont icon-sousuo" @click="getUserList"></el-button>
                </el-input>
                <!--  -->
            </el-col>
                <el-col :span="4">
                <el-button type="primary"  @click="addDialogVisible = true"></el-button>   
            </el-col>       
        </el-row>

        <!--  -->
        <el-table :data="userList" border stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="username" align="center"></el-table-column>
            <el-table-column label="" prop="email" align="center"></el-table-column>
            <el-table-column label="" prop="password" align="center"></el-table-column>
            <el-table-column label="" prop="role" width="120" align="center"></el-table-column>
            <el-table-column label="" prop="visits" width="90" align="center"></el-table-column>
            <el-table-column label="" prop="state" width="90" align="center">
                <!-- scope.row  -->
                <template slot-scope="scope"><!---->
                    <el-switch v-model="scope.row.state" @change="userStateChanged(scope.row)"></el-switch>
                </template>                 
            </el-table-column>
            <el-table-column label="" prop="picbase64" width="360" align="center">
                <template slot-scope="scope">
                  <img :src=scope.row.touxiang min-width="70" height="70">
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <!--  -->
                    <el-tooltip effect="dark" content="" placement="top-start" :enterable="false">
                        <el-button type="primary"  icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                    </el-tooltip>
                    <!--  -->
                    <el-tooltip effect="dark" content="" placement="top-start" :enterable="false">
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteUser(scope.row.id)"></el-button>
                    </el-tooltip>
                    <!--  -->
                    <el-tooltip effect="dark" content="" placement="top-start" :enterable="false">
                        <el-button type="warning" icon="el-icon-setting" size="mini"></el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <!--  size-change: current-page: layout-->
        <div>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pageNum"
                :page-sizes="[1, 2, 5, 100]"
                :page-size="queryInfo.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
            <el-form-item label="" prop="username">
                <el-input v-model="addForm.username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input v-model="addForm.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
                <el-input v-model="addForm.email"></el-input>
            </el-form-item>
            <el-form-item  label="" prop="touxiang">
                <template>
                        <img :src="addForm.touxiang" />
                        <el-form-item>
                        <el-upload
                            class="upload-demo"
                            ref="upload"
                            action=""
                            :http-request="upload"
                            multiple=""
                            :auto-upload="false"
                        >
                            <el-button slot="trigger" size="small" type="primary"
                            ></el-button
                            >
                            <el-button
                            style="margin-left: 10px"
                            size="small"
                            type="success"
                            @click="submitUpload"
                            ></el-button
                            >
                            <div slot="tip" class="el-upload__tip">
                            
                            </div>
                        </el-upload>
                        </el-form-item>
                </template>
            </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false"></el-button>
            <el-button type="primary" @click="addUser"></el-button>
        </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
            <el-form-item label="" prop="username">
                <el-input v-model="editForm.username" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input v-model="editForm.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
                <el-input v-model="editForm.email"></el-input>
            </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"></el-button>
            <el-button type="primary" @click="editUserInfo"></el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
    created(){
        this.getUserList();
    },
    data(){
        return{
            //
            queryInfo:{
                query:"",
                pageNum:1,//
                pageSize:5//
            },
            userList:[],
            total:0,//
            addDialogVisible: false,//
            // 
            addForm:{
                username:'',
                password:'',
                email:'',
                touxiang:'',
            },
            //
            editForm:{
                
            },
            ///
            editDialogVisible:false,
            // 
             addFormRules: {
                username: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 12, message: ' 5  12 ', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '', trigger: 'change' },
                    { min: 6, max: 12, message: ' 6  12 ', trigger: 'blur'}
                ],
                email: [
                    { required: true, message: '', trigger: 'change' },
                    { min: 5, max: 30, message: '', trigger: 'blur'}
                ],
             },
            // 
            editFormRules:{
                password: [
                    { required: true, message: '', trigger: 'change' },
                    { min: 6, max: 12, message: ' 6  12 ', trigger: 'blur'}
                ],
                email: [
                    { required: true, message: '', trigger: 'change' },
                    { min: 5, max: 15, message: '', trigger: 'blur'}
                ],
            }
        }
    },
    methods:{
        async getUserList(){
            const {data:res} = await this.$http.get("alluser",{params:this.queryInfo});
            this.userList = res.users;
            this.total = res.userCounts;
        },
        // 
        handleSizeChange(newSize){
            this.queryInfo.pageSize = newSize;
            this.getUserList();
        },
        // pageNum
        handleCurrentChange(newPage){
            this.queryInfo.pageNum = newPage; 
            this.getUserList();   
        },
        async userStateChanged(userInfo){
            const {data:res} = await this.$http.put(`userstate?id=${userInfo.id}&state=${userInfo.state}`);
            if (res!="success"){
                userInfo.id = !userInfo.id;
                return this.$message.error("");
            }
            this.$message.success("!!!");
        },
        addDialogClosed(){
            this.$refs.addFormRef.resetFields();
        },
        addUser(){
            this.$refs.addFormRef.validate(async valid=>{
                if(!valid)return;
                const {data:res} = await this.$http.post("adduser",this.addForm);
                if(res!="success"){
                    return this.$message.error("");
                }
                this.$message.success("!!!");
                this.addDialogVisible = false;
                this.getUserList();
            });
        },
        async deleteUser(id){
            const confirmResult = await this.$confirm("","",{
                confirmButtonText:'',
                cancelButtonText:'',
                type:'warning'
            }).catch(err => err)
            if(confirmResult!='confirm'){
                return this.$message.info("");
            }
            const {data:res} = await this.$http.delete("deleteuser?id="+id);
            if(res != "success"){
                return this.$message.error("");
            }
            this.$message.success("");
            this.getUserList();
        },
        //
        async showEditDialog(id){
            const {data:res} = await this.$http.get("getupdate?id="+id);
            this.editForm = res;
            this.editDialogVisible = true;
        },
        // 
        editDialogClosed(){
            this.$refs.editFormRef.resetFields();
        },
        // 
        editUserInfo(){
            this.$refs.editFormRef.validate(async valid =>{
                if(!valid) return;
                const {data:res} = await this.$http.put("edituser",this.editForm);
                if(res!="success")return this.$message.error("");
                this.$message.success("");
                this.editDialogVisible = false;
                this.getUserList();
            })
        },
        //
            submitUpload() {
            this.$refs.upload.submit();
            },
            //onchanne
            handleChange(file, fileList) {
            this.fileList = fileList;
            },
            handlePreview(file) {
            },
            async upload(file) {
                let fd = new FormData();
                //raw
                fd.append("files", file.file);
                await this.$http.post(`/uploadUi`, fd).then((res) => {
                this.addForm.touxiang = res.data;
                alert("");
                });
            },
    }
}
</script>

<style scoped>

</style>
