<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item class="activepage"></el-breadcrumb-item>
        <el-breadcrumb-item ></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      
        <el-row :gutter="30">
            <el-col :span="10">
                <!--  -->
                <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getClientList">
                    <el-button slot="append" icon="iconfont icon-sousuo" @click="getClientList"></el-button>
                </el-input>
                <!--  -->
            </el-col>
                <el-col :span="4">
                <el-button type="primary"  @click="addDialogVisible = true"></el-button>   
            </el-col> 
            
            <el-col :span="4">
                <el-select v-model="value" placeholder="" @change="ClientLevelSelect(value)">
                        <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
            </el-col>      
        </el-row>

        <!--  -->
        <el-table  :data="clientList" border stripe :default-sort = "{order: 'descending'}">
            <el-table-column fixed type="index" label="" :index="indexMethod"  width="60" align="center" sortable></el-table-column>
            <!-- <el-table-column prop="clientid"   width="60" align="center" sortable></el-table-column> -->
            <el-table-column label="" prop="corporationname"  width="250" align="center" show-overflow-tooltip="true" sortable></el-table-column>            
            <el-table-column label="" prop="clientname" width="180" align="center" show-overflow-tooltip="true" sortable></el-table-column>
            <el-table-column label="" prop="clientrole" width="180" align="center" show-overflow-tooltip="true" sortable></el-table-column>
            <el-table-column label="" prop="clienttelephone" width="250" align="center" sortable></el-table-column>
            <el-table-column label="" prop="clientinterview" width="250" align="center" sortable show-overflow-tooltip="true"></el-table-column>
            <el-table-column label="" prop="clientlevel" width="180" align="center" sortable>
                <template slot-scope="scope">
                    <el-select v-model="scope.row.clientlevel" placeholder="" @change="ClientLevelChanged(scope.row)">
                        <el-option
                        v-for="item in options2"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </template>
            </el-table-column>

            <el-table-column label="" align="center" width="320">
                <template slot-scope="scope">
                    <!--  -->
                    <el-tooltip effect="dark" content="" placement="top-start" :enterable="false">
                        <el-button type="primary"  icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.clientid)"></el-button>
                    </el-tooltip>
                    <!--  -->
                    <el-tooltip effect="dark" content="" placement="top-start" :enterable="false">
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteClient(scope.row.clientid)"></el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        
        <!--  size-change: current-page: layout-->
        <div>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pageNum"
                :page-sizes="[1, 5, 10, 100]"
                :page-size="queryInfo.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>

    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="120px">
            <el-form-item label="" prop="clientname">
                <el-input v-model="addForm.clientname"></el-input>
            </el-form-item>
            <el-form-item label="" prop="corporationname">
                <template>
                <el-select v-model="addForm.corporationname" clearable placeholder="" 
                filterable
                allow-create>
                    <el-option
                    v-for="item in corporationNameList"
                    :key="item"
                    :label="item"
                    :value="item">
                    </el-option>
                </el-select>
                </template>
            </el-form-item>
            <el-form-item label="" prop="clientrole">
                <template>
                <el-select v-model="addForm.clientrole" clearable placeholder="" 
                filterable
                allow-create>
                    <el-option
                    v-for="item in roleOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                </template>
            </el-form-item>
            <el-form-item label="" prop="clienttelephone">
                <template>
                    <el-input v-model.number="addForm.clienttelephone"></el-input>
                </template>
            </el-form-item>
            <el-form-item label="" prop="clientinterview">
                <template>
                    <el-input v-model="addForm.clientinterview"></el-input>
                </template>
            </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false"></el-button>
            <el-button type="primary" @click="addClient"></el-button>
        </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="120px">
            <el-form-item label="" prop="clientname">
                <el-input v-model="editForm.clientname"></el-input>
            </el-form-item>
            <el-form-item label="" prop="corporationname">
                <template>
                <el-select v-model="editForm.corporationname" clearable placeholder="" 
                filterable
                allow-create>
                    <el-option
                    v-for="item in corporationNameList"
                    :key="item"
                    :label="item"
                    :value="item">
                    </el-option>
                </el-select>
                </template>
            </el-form-item>
            <el-form-item label="" prop="clientrole">
                <template>
                <el-select v-model="editForm.clientrole" clearable placeholder="" 
                filterable
                allow-create>
                    <el-option
                    v-for="item in roleOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                </template>
            </el-form-item>
            <el-form-item label="" prop="clienttelephone">
                <template>
                    <el-input v-model.number="editForm.clienttelephone"></el-input>
                </template>
            </el-form-item>
            <el-form-item label="" prop="clientinterview">
                <template>
                    <el-input v-model="editForm.clientinterview"></el-input>
                </template>
            </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"></el-button>
            <el-button type="primary" @click="editClient"></el-button>
        </span>
        
    </el-dialog>
  </div>
</template>

<script>
export default {
  created(){
        this.getClientList();
        this.getCorporationNameList();
    },
  data(){
    return{
        roleOptions: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }],
        options: [{
          value: 0,
          label: ''
        },{
          value: 1,
          label: 'A'
        }, {
          value: 2,
          label: 'B'
        }, {
          value: 3,
          label: 'C'
        },{
          value: 4,
          label: 'D'
        },{
          value: 5,
          label: ''
        }
        ],
        options2: [{
          value: 1,
          label: 'A'
        }, {
          value: 2,
          label: 'B'
        }, {
          value: 3,
          label: 'C'
        },{
          value: 4,
          label: 'D'
        },{
          value: 5,
          label: ''
        }
        ],
        value:"",
        url:'',
        name:'',
        value1:'',
      addFormRules: {
             clientname: [
                { required: true, message: '', trigger: 'change' },
                { min: 2, max: 30, message: '2-30', trigger: 'blur'}
            ],
            clientrole: [
                { required: true, message: '', trigger: 'change' }
            ],
            corporationname: [
                { required: true, message: '', trigger: 'change' }
            ],
            clienttelephone: [
                { required: true, message: '', trigger: 'change', },
                { type: 'number', message: ''},
                
            ],
      }, 
      // 
        editFormRules:{
            clientname: [
                { required: true, message: '', trigger: 'change' },
                { min: 2, max: 30, message: '2-30', trigger: 'blur'}
            ],
            clientrole: [
                { required: true, message: '', trigger: 'change' }
            ],
            corporationname: [
                { required: true, message: '', trigger: 'change' }
            ],
            clienttelephone: [
                { required: true, message: '', trigger: 'change', },
                
            ],
        },     
      addForm:{
            },
      editDialogVisible:false,
      editForm:{
                
            },
      addDialogVisible:false,
      queryInfo:{
                clientlevelInfo:0,
                query:"",
                pageNum:1,//
                pageSize:5//
            },
      clientList:[],
      corporationNameList:[],
      corporationRoleList:[],
      total:0,
      clientrole:'',
      corporationname:'',
    }
  },
  methods:{
        //
        async DealStateChanged(clientInfo){
            const {data:res} = await this.$http.put(`dealstate?id=${clientInfo.clientid}&isdeal=${clientInfo.isdeal}`);
            if (res!="success"){
               clientInfo.clientid = !clientInfo.clientid;
                return this.$message.error("");
            }
            this.$message.success("!!!");
        },
        //
      async showEditDialog(id){
          const {data:res} = await this.$http.get("getupdateclient?id="+id);
          this.editForm = res;
          this.editDialogVisible = true;
      },
  // 
      editDialogClosed(){
          this.$refs.editFormRef.resetFields();
      },
         async getClientList(){
            const {data:res} = await this.$http.get("allclient",{params:this.queryInfo});
            this.clientList = res.clients;
            this.total = res.clientCounts;
        },
         async getCorporationNameList(){
            const {data:res} = await this.$http.get("allcorporationname");
            this.corporationNameList = res.corporations;
            console.log(this.corporationNameList)
        },

        // pageNum
        handleCurrentChange(newPage){
            this.queryInfo.pageNum = newPage; 
            this.getClientList();   
        },
        // 
        handleSizeChange(newSize){
            this.queryInfo.pageSize = newSize;
            this.getClientList();
        },
        addDialogClosed(){
            this.$refs.addFormRef.resetFields();
        }, 
        //
        addClient(){
            console.log(this.addForm);
            this.$refs.addFormRef.validate(async valid=>{
                if(!valid)return;
                const {data:res} = await this.$http.post("addclient",this.addForm);
                if(res!="success"){
                    return this.$message.error("");
                }
                this.$message.success("!!!");
                this.addDialogVisible = false;
                this.getClientList();      
            });
        },    
        // 
        editClient(){
            this.$refs.editFormRef.validate(async valid =>{
                if(!valid) return;
                const {data:res} = await this.$http.put("editclient",this.editForm);
                if(res!="success")return this.$message.error("");
                this.$message.success("");
                this.editDialogVisible = false;
                this.getClientList();
            })
        },
      //
      async deleteClient(id){
            const confirmResult = await this.$confirm("","",{
                confirmButtonText:'',
                cancelButtonText:'',
                type:'warning'
            }).catch(err => err)
            if(confirmResult!='confirm'){
                return this.$message.info("");
            }
            const {data:res} = await this.$http.delete("deleteclient?id="+id);
            if(res != "success"){
                return this.$message.error("");
            }
            this.$message.success("");
            this.getClientList();
        },
        // 
        async ClientLevelChanged(clientlevelInfo){
            const {data:res} = await this.$http.put(`clientlevel?clientid=${clientlevelInfo.clientid}&clientlevel=${clientlevelInfo.clientlevel}`);
            if (res!="success"){
                return this.$message.error("");
            }
            this.$message.success("!!!");
        }, 
        // 
        async ClientLevelSelect(clientlevelInfo){
            console.log(clientlevelInfo)
            if(clientlevelInfo==0){
                this.getClientList();
            }else{
                this.queryInfo.clientlevelInfo=clientlevelInfo
                console.log("!!!",clientlevelInfo,this.queryInfo)
                const {data:res} = await this.$http.get("clientlevelselect",{params:this.queryInfo});
                this.clientList = res.clients;
                this.total = res.clientCounts;
            } 
        },
        indexMethod(index) {
            return index * 1;
        }
  }
}
</script>

<style scoped>

</style>