<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item class="activepage"></el-breadcrumb-item>
        <el-breadcrumb-item ></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      
        <el-row :gutter="30">
            <el-col :span="10">
                <!--  -->
                <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getCorporationList">
                    <el-button slot="append" icon="iconfont icon-sousuo" @click="getCorporationList"></el-button>
                </el-input>
                <!--  -->
            </el-col>
                <el-col :span="4">
                <el-button type="primary"  @click="addDialogVisible = true"></el-button>   
            </el-col>       
        </el-row>

        <!--  -->
        <el-table  :data="corporationList" border  :default-sort = "{ order: 'descending'}" :row-class-name="tableRowClassName">
            <el-table-column fixed type="index" label="" :index="indexMethod"  width="60" align="center" sortable></el-table-column>
            <el-table-column fixed label="id" prop="corporationid"  width="60" align="center" sortable></el-table-column>
            <el-table-column fixed label="" prop="corporationname"  width="240" align="center" show-overflow-tooltip="true" sortable>
                <template slot-scope="scope"><!---->
                    <el-popover
                    placement="right"
                    width="850"
                    trigger="click">
                     <el-table :data="ClientList">
                        <el-table-column width="150" property="clientname" label=""></el-table-column>
                        <el-table-column width="150" property="clientrole" label=""></el-table-column>
                        <el-table-column width="200" property="clienttelephone" label="" show-overflow-tooltip="true"></el-table-column>
                        <el-table-column width="200" property="clientinterview" label="" show-overflow-tooltip="true"></el-table-column>
                        <el-table-column width="150" property="clientlevel" label="">
                            <template slot-scope="scope">
                                {{scope.row.clientlevel | levelFromat}}
                            </template>
                        </el-table-column>
                    </el-table>
                        <el-button slot="reference" @click="getClientList(scope.row.corporationname)">{{scope.row.corporationname}}</el-button>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column fixed label="" prop="corporationaddress" width="180" align="center" show-overflow-tooltip="true" sortable></el-table-column>
            <el-table-column fixed label="" prop="isvisit" width="120" align="center" sortable>
                <!-- scope.row  -->
                <template slot-scope="scope"><!---->
                    <el-switch v-model="scope.row.isvisit" @change="VisitStateChanged(scope.row)"></el-switch>
                </template>
            </el-table-column>
