            <el-table-column fixed label="2" prop="corporationsupplement2" width="180" align="center"  show-overflow-tooltip="true"></el-table-column>
            <el-table-column fixed label="" align="center" width="150">
                <template slot-scope="scope">
                    <!-- <el-tooltip effect="dark" content="" placement="top-start" :enterable="false">
                        <el-button  type="primary" @click="showCheckDialog(scope.row)" icon="el-icon-edit" size="mini"></el-button>
                    </el-tooltip> -->
