        // 
        editCorporation(){
            if(this.editForm.corporationgxdqdate=="null"){
                 this.editForm.corporationgxdqdate = "2000-01-01"
             }
             if(this.editForm.corporationrqdqdate=="null"){
                 this.editForm.corporationrqdqdate = "2000-01-01"
             }
